.leaflet-container {
    height: 500px;
    width: 500px;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
    transition: all 200ms ease-in-out;
    margin-top: 1rem;
}

.leaflet-container:hover {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    transition: all 200ms ease-in-out;
}